
import $ from 'jquery'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XImg from 'vux/src/components/x-img/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


let isAndroid = 0
let browser = {
versions: () => {
let u = navigator.userAgent
return {
mobile: !!u.match(/AppleWebKit.*Mobile.*/), // 是否为移动终端
ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios终端
android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 
}
}
}
browser.versions()

export default {
directives: {
TransferDom
},
components: {
XImg, XHeader, Swiper, SwiperItem, Popup, Flexbox, FlexboxItem
},
data () {
return {
publicPath: 'https://yjj5855.github.io/activity-lt/dist/',
list: [
],

drawerVisibility: false,
bannerList: [
'static/home/banner-1.png',
'static/home/banner-2.png',
'static/home/banner-3.png',
'static/home/banner-4.png'
],
bannerIndex: 0,

videoList: [

],
videoIndex: 0,

baobaoList: [
'static/home/baobao-1.png',
'static/home/baobao-2.png',
'static/home/baobao-3.png',
'static/home/baobao-4.png',
'static/home/baobao-5.png'
],
baobaoIndex: 0,

videoSrc: ''
}
},
mounted () {
if (browser.versions.android) {
isAndroid = 1
$('#video-1').attr({'controls': 'controls'})
} else {
$('body').addClass('animate')
}
},
methods: {
playVideo (mp4) {
this.videoSrc = `static/home/${mp4}`
this.$nextTick(() => {
$('#video-1')[0].currentTime = 0.5
$('#video').addClass('show')
if (isAndroid === 1) {
$('#video-1')[0].play()
} else {
setTimeout(() => {
$('#video-1')[0].play()
}, 500)
}
})
},
scrollTop () {
$(window).scrollTop(0)
}
}
}

window.videoUpdate = function (event) {
console.log(event.src)
let miao = 100
if (event.src.includes('zbyrsp')) {
miao = 22
} else if (event.src.includes('hxsp')) {
miao = 36
}
if (event.currentTime >= miao) {
$('#video').removeClass('show')
}
}



